:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(146, 68%, 55%);
  --toggle-bg-end: hsl(210, 78%, 56%);
  --toggle-bg-light: hsl(220, 22%, 74%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-light-switcher: hsl(228, 46%, 96%);
  --white: hsl(0, 0%, 100%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text: hsl(228, 34%, 66%);
  --light-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(222, 50%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-gray-text: hsl(230, 12%, 44%);
  --light-dark-text: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --card-bg: var(--light-card-bg);
  --card-hover: var(--light-card-hover);
  --text-color: var( --light-gray-text);
  --text-body-color: var(--light-dark-text);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-switcher: var(--toggle-light-switcher);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --card-bg: var(--dark-card-bg);
    --card-hover: var(--dark-card-hover);
    --text-color: var(--dark-text);
    --text-body-color: var(--white);
    --toggle: var(--white);
    --toggle-bg: linear-gradient(
        225deg,
        var(--toggle-bg-start) 0%,
        var(--toggle-bg-end) 98.02%
    );
    --toggle-switcher: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --card-bg: var(--light-card-bg);
  --card-hover: var(--light-card-hover);
  --text-color: var( --light-gray-text);
  --text-body-color: var(--light-dark-text);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-switcher: var(--toggle-light-switcher);
}

body.dark {
  --background: var(--dark-bg);
  --card-bg: var(--dark-card-bg);
  --card-hover: var(--dark-card-hover);
  --text-color: var(--dark-text);
  --text-body-color: var(--white);
  --toggle: var(--white);
  --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-start) 0%,
      var(--toggle-bg-end) 98.02%
  );
  --toggle-switcher: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-body-color);
}

.screenreader-only {
  opacity: 0;
  position: absolute;
  top: auto;
  left: -10000px;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.hidden {
  display: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.grid-gap-10 {
  gap: 0.625rem;
}
.grid-gap-20 {
  gap: 1.25rem;
}
.grid-gap-30 {
  gap: 1.875rem;
}
.grid-gap-40 {
  gap: 2.5rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  gap: 0.3rem 0.75rem;
  border: none;
  margin: 0;
  align-items: center;
}
.toggle__label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle__label[for=dark] {
  grid-column: 1/2;
}
.toggle__label:hover {
  cursor: pointer;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  height: 1.5rem;
}
.toggle__wrapper input[type=radio] {
  opacity: 0;
  margin: 0 -0.25rem 0 -0.25rem;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle__wrapper input[type=radio]:hover {
  cursor: pointer;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: var(--toggle-bg);
  z-index: -1;
  pointer-events: none;
}
.toggle__switcher {
  display: block;
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-switcher);
  transition: all 350ms ease-in-out;
}
.toggle input[type=radio]#light:checked ~ .toggle__switcher,
.toggle input[type=radio]:focus#light:checked ~ .toggle__switcher {
  right: 0.1875rem;
  left: calc(100% - 1.3125rem);
}
.toggle input[type=radio]#system:checked ~ .toggle__switcher,
.toggle input[type=radio]:focus#system:checked ~ .toggle__switcher {
  right: auto;
  left: 50%;
  translate: -50%;
}

.card {
  position: relative;
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 1.5625rem;
  text-align: center;
  border-radius: 0.3125rem;
  overflow: hidden;
  cursor: pointer;
  transition: background-color 150ms ease-in-out;
}
.card:hover {
  background-color: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-image: linear-gradient(-225deg, var(--instagram-start) 0%, var(--instagram-middle) 50.91%, var(--instagram-end) 98.02%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__icon {
  width: 1.25rem;
  height: 1.25rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__body {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-body-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2.125rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.125rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.card-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: repeat(2, auto);
  gap: 1.875rem;
  justify-items: start;
  padding: 0.9375rem 1.5625rem;
}
.card-grid__title {
  align-self: center;
  text-align: left;
  font-size: 0.875rem;
  font-weight: 700;
}
.card-grid__icon {
  justify-self: end;
  align-self: center;
  width: 1.25rem;
  height: 1.25rem;
}
.card-grid__count {
  font-weight: 700;
  color: var(--text-body-color);
}
.card-grid__percentage {
  justify-self: end;
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
}
.card-grid__percentage--up {
  color: var(--limegreen);
}
.card-grid__percentage--down {
  color: var(--brightred);
}